/* =================================== */

/*  Basic Style 
/* =================================== */
html, body {
    height: 100%;
}
body {
	color: $text-color;
	font-family: $primary-font;
	font-size: 16px;
	font-weight: 400;
	line-height: 26px;
	position: relative;
	min-height: 100vh;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: $title-font;
	color: $title-color;
	margin-bottom: 0;
	margin-top: 0;
	font-weight: 500;
	line-height: 1.13;
}

h1,
.h1 {
	font-size: 48px;
}

h2,
.h2 {
	font-size: 32px;
}

h3,
.h3 {
	font-size: 24px;
}

h4,
.h4 {
	font-size: 20px;
}

h5,
.h5 {
	font-size: 16px;
}

h6,
.h6 {
	font-size: 14px;
}

::selection {
	background-color: $primary-color;
	color: $white;
}

::-webkit-input-placeholder {
	/* WebKit, Blink, Edge */
	color: #999999;
	font-weight: 300;
}

:-ms-input-placeholder {
	/* Internet Explorer 10-11 */
	color: #999999;
	font-weight: 300;
}

::-ms-input-placeholder {
	/* Microsoft Edge */
	color: #999999;
	font-weight: 300;
}

ol,
ul {
	margin: 0;
	padding: 0;
	list-style: none;
}

select {
	display: block;
}

figure {
	margin: 0;
}

a {
	@include transition(all 0.3s ease 0s);
}

iframe {
	border: 0;
}

a,
a:focus,
a:hover {
	text-decoration: none;
	outline: 0;
}

.btn.active.focus,
.btn.active:focus,
.btn.focus,
.btn.focus:active,
.btn:active:focus,
.btn:focus {
	text-decoration: none;
	outline: 0;
}

.card-panel {
	margin: 0;
	padding: 60px;
}

/**
 *  Typography
 *
 **/

.btn i,
.btn-large i,
.btn-floating i,
.btn-large i,
.btn-flat i {
	font-size: 1em;
	line-height: inherit;
}

.gray-bg {
	background: #f9f9ff;
}

td,
th {
	border-radius: 0px;
}

/**
 * For modern browsers
 * 1. The space content is one way to avoid an Opera bug when the
 *    contenteditable attribute is included anywhere else in the document.
 *    Otherwise it causes space to appear at the top and bottom of elements
 *    that are clearfixed.
 * 2. The use of `table` rather than `block` is only necessary if using
 *    `:before` to contain the top-margins of child elements.
 */

.clear {
	&::before,
	&::after {
		content: " ";
		display: table;
	}
	&::after {
		clear: both;
	}
}

