.relative {
	position: relative;
}

.overflow-hidden {
	overflow: hidden;
}

.overlay {
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
}

.container {
	&.fullwidth {
		width: 100%;
	}
	&.no-padding {
		padding-left: 0;
		padding-right: 0;
	}
}

.no-padding {
	padding: 0;
}

.section-bg {
	background: #f9f9f9;
}

.no-flex-xs {
	@include mobile {
		display: block !important;
	}
}

.row {
	&.no-margin {
		margin-left: 0;
		margin-right: 0;
	}
}

.alert-msg {
	color: #228b22;
}

.gradient-bg {
	@include gradient(90deg, $primary-color2, $primary-color);
}

.gradient-bg-2 {
	@include gradient(90deg, $primary-color3, $primary-color4);
}

.gradient-text-color {
	@include gradient(90deg, $primary-color2, $primary-color);
	@include background-clip();
	color: transparent;
}

.watch {
	font-family: $title-font;
	font-size: 12px;
	font-weight: 500;
	color: $white;
	margin-left: 20px;
	@media (max-width: 767px) and (min-width: 634px) {
		color: $title-color;
	}
}

.fullscreen {
	@media (max-width: 767px) and (min-width: 634px) {
		height: 600px !important;
	}
}
